<template>
     <el-row>
       <el-col :span="18" :offset="3" v-if="post" class="html-img">
           <h2 class="text-center"> {{post.title}} </h2>
           <el-row justify="center">
               <el-col :xs="8" :sm="8" :md="4" :lg="2" :xl="2" >

                <el-avatar :size="50" src="https://empty" style="float:right"  @error="errorHandler">
                    <img src="../../../public/pp-img2.jpg"/>
                </el-avatar>
               </el-col>
                <el-col :xs="16" :sm="16" :md="8" :lg="6" :xl="4" style="margin-top: auto; margin-bottom: auto;float:left;color:#a3a098">
                     <b>Gülşah Alıcı · <small>{{ timeConvert(post.created_at) }}</small></b> 
                </el-col>
            </el-row>
          
           <div v-html="post.content"></div>
       </el-col>
     </el-row>
</template>

<script>
import { useRouter } from 'vue-router'
import { useLoadPost } from '@/firebase'
import 'firebase/firestore'
import 'firebase/auth'
import { ref } from 'vue'

export default {
  setup() {
    const router = useRouter()

    const slug = router.currentRoute.value.params.slug

    var post = ref(null)
    useLoadPost(slug).then((data) =>
    {
        console.log("hey",data)
        post.value = data
    })
    

    const timeConvert = (timestamp) => {
      var d = new Date(timestamp.seconds * 1000)
      var curr_date = d.getDate()
      var curr_month = d.getMonth() + 1
      var curr_year = d.getFullYear()

      return curr_date + "-" + curr_month + "-" + curr_year
    }

    return {
        post,
        timeConvert
    }
  }
}
</script>

<style >
.html-img img {
    max-width: 100% !important;
}

