<template>

  <el-row :gutter="20">
    <el-col :span="18" :offset="3" v-for="post in sortedPosts" :key="post.id">
      <el-card :body-style="{ padding: '0px' }" shadow="hover" style="martin-top: 30px; margin-bottom: 30px">
        <el-row style="padding: 14px" align="middle" justify="center">
          <el-col :xs="24" :sm="24" :md="14" :lg="16" :xl="12">
            <div class="grid-content bg-purple">
              <p class="text-center">
                <b>{{ post.title }}</b>
              </p>
              <div class="bottom">
                {{ post.summary }} ..
                <el-link :underline="false" type="primary" :href="'/blog/' + post.slug">      
                  Read more
                </el-link>
                <span v-if="isLoggedIn && router.currentRoute.value.fullPath == '/admin'" style="float:right">
                  <br>
                  <el-link :underline="false" type="success" :href="'/admin/posts/' + post.slug">
                    <el-button type="primary" :icon="Edit" circle />
                  </el-link>
                  <el-button type="danger" :icon="Delete" circle @click="dialogVisible = true, selectedPost = post"/>
                </span>
                <small style="float:right; color:#a3a098">
                  <b>Gülşah Alıcı</b> <br/>
                  <b style="margin-left: 6p"> {{ timeConvert(post.created_at) }} </b>
                </small>
              </div>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :md="10" :lg="8" :xl="12" style="margin-top: 10px; padding: 20px;" class="text-center">
            <el-image v-if="post.image && post.image != ''" :src="post.image" lazy />
            <el-image v-else src="https://pineco.de/wp-content/uploads/2018/10/laravel-package-development-basics.png" lazy />
          </el-col>
        </el-row>
      </el-card>
    </el-col>
    <el-dialog
      v-model="dialogVisible"
      title="Remove Post"
      width="30%"
    >
      <span>Are you sure want to delete this <b>{{ selectedPost.slug }}</b>?</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="removePost()" >Confirm</el-button
          >
        </span>
      </template>
    </el-dialog>
  </el-row>
</template>

<script>
import { useLoadPosts, deletePost } from '@/firebase'
import { onBeforeMount, computed, ref } from 'vue'
import firebase from 'firebase/app'
import { useRouter } from 'vue-router'
import { Edit, Delete } from '@element-plus/icons'

export default {
  props: {
    admin: Object
  },
  setup() {
    const posts = useLoadPosts()
    const isLoggedIn = ref(false)
    const router = useRouter()
    const dialogVisible = ref(false)
    const selectedPost = ref(null)

    const removePost = () => {
      deletePost(selectedPost.value.slug)
      dialogVisible.value = false
    }

    const timeConvert = (timestamp) => {
      var d = new Date(timestamp.seconds * 1000)
      var curr_date = d.getDate()
      var curr_month = d.getMonth() + 1
      var curr_year = d.getFullYear()

      return curr_date + "-" + curr_month + "-" + curr_year
    }

    var sortedPosts = computed(() => {
       return posts.value.slice().sort((a, b) => {
        return new Date(b.created_at.seconds * 1000) - new Date(a.created_at.seconds * 1000)
      })
    })
    onBeforeMount(() => {
        
        firebase.auth().onAuthStateChanged((user) => {
        if(user) {
            isLoggedIn.value = true
        }
        else {
            isLoggedIn.value = false
        }
      }) 
    })

    return {
      posts,
      timeConvert,
      sortedPosts,
      isLoggedIn,
      router,
      Edit,
      Delete,
      dialogVisible,
      removePost,
      selectedPost
    }
  }

}
</script>

<style scoped>
.img-border {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 1px;
}
img {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  max-width: 250px;
  width: auto;
}
</style>